import { Component, OnInit } from '@angular/core';
import {Http,Response} from '@angular/http'
import {Config} from '../config'
import {Router} from '@angular/router'
import * as md5 from 'md5'


@Component({
  selector: 'app-search-devices',
  templateUrl: './search-devices.component.html',
  styleUrls: ['./search-devices.component.css']
})
export class SearchDevicesComponent implements OnInit {
  key:string;
  deviceid:string = "eabf0612-5fc7-11e7-907b-a6006ad3dba0";
  device:any;
  message:string;
  bindSuccess:boolean;
  bindError:boolean;
  deviceMessage:string;
  constructor(private http:Http, private config:Config, private router:Router) { 
  }

  ngOnInit() {
  }

  

  bind(){
    if(this.key == undefined || this.key == ""){
      alert('Please add Key first')
      return
    }
    var data = {
      'did': this.deviceid,
      'uid': this.config.uid,
      'key': md5(this.key)
    }
    console.log(data)
    this.http.post(this.config.url + '/api/devices/binding',data).subscribe((s:Response)=>{
      console.log(s.json())
      var body = s.json()
      this.deviceMessage =  body.message;
      if(body.status == "SUCCESS"){
        this.bindSuccess = true;
        this.bindError = false;
      }
      else{
        this.bindSuccess = false;
        this.bindError = true;
      }

    })
  }

  searchid(){
    this.device = undefined
    if(this.deviceid == undefined || this.deviceid == ""){
      alert('Please add device id first');
    }

    this.http.post(this.config.url + '/api/devices/id', {'did': this.deviceid})
    .subscribe((s:Response)=>{
      console.log(s.json())
      //eabf0612-5fc7-11e7-907b-a6006ad3dba0
      if(s.json().length == 0){
        this.message = "Device not found."
        return;
      }
      this.message = undefined

      this.device = s.json()[0]
      var split = this.device.did.split("-")
      this.device.short_did = split[split.length-1]
      console.log(this.device)
    })
  }
}



