import { Component, OnInit , AfterViewInit} from '@angular/core';
declare var $:any;
import {Http} from '@angular/http'
import {AngularFireAuthProvider,AngularFireAuth } from 'angularfire2/auth'
import {AngularFireDatabase} from 'angularfire2/database'
import {Observable} from 'rxjs/Observable'
import * as firebase from 'firebase/app';
import {Router} from '@angular/router'
import {LoginGuard} from './login-guard'
import {Config} from '../config'
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit, AfterViewInit {
  user: Observable<firebase.User>;
   auth:firebase.Promise<any>
   name:string;

   email:string;
   password:string;
   register_email:string;
   register_password:string;

   message:string;
  constructor(private afAuth:AngularFireAuth,private db:AngularFireDatabase,private http:Http, private router:Router
  ,private loginguard:LoginGuard,private config:Config) { 
    this.user = afAuth.authState;
    
  }

  ngOnInit() {
    this.afAuth.authState.subscribe(s=>{
       if(!s){
        console.log("NOT LOGGED IN");
        return;
       }
       this.config.uid = s.uid
       this.loginguard.isLoggedIn = true;
       console.log(s, this.loginguard.isLoggedIn)
       this.router.navigateByUrl('/dashboard')
    });
  }
  ngAfterViewInit() {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
    $(function() {
        $('#login-form-link').click(function(e) {
        $("#login-form").delay(100).fadeIn(100);
        $("#register-form").fadeOut(100);
        $('#register-form-link').removeClass('active');
        $(this).addClass('active');
        e.preventDefault();
      });
      $('#register-form-link').click(function(e) {
          $("#register-form").delay(100).fadeIn(100);
          $("#login-form").fadeOut(100);
          $('#login-form-link').removeClass('active');
          $(this).addClass('active');
          e.preventDefault();
      });
    });
  }

  userRegister(){
    this.afAuth.auth.createUserWithEmailAndPassword(this.register_email, this.register_password)
    .then((authState:any)=>{
      console.log("REGISTER ", authState)
    })
    .catch(error=>{
      console.log("ERROR ", error)
    })
  }

  login(){
    console.log(this.email , this.password)
    if(this.email == "" || this.password == "" || this.email == undefined || this.password == undefined){
      console.log("please fill email and password!")
      this.message = "please fill email and password!"
      return
    }
    this.afAuth.auth.signInWithEmailAndPassword(this.email,this.password)
    .then(authState=>{
      this.loginguard.isLoggedIn = true;
      this.router.navigateByUrl('/dashboard');
      
    })
    .catch(error=>{
      console.log("ERROR ", error.message)
      this.message = error.message
    })
  }

  logout(){
    this.loginguard.isLoggedIn = false;
    this.afAuth.auth.signOut()
  }

  

}





