import { Component, OnInit } from '@angular/core';
import {Http,Response} from '@angular/http'
import {Config} from '../config'
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Router} from '@angular/router';

@Component({
  selector: 'app-device-list',
  templateUrl: './device-list.component.html',
  styleUrls: ['./device-list.component.css']
})
export class DeviceListComponent implements OnInit {
  devices:Observable<any[]>;
  constructor(private http:Http,private config:Config,private router:Router) { 
     this.devices = this.http.post(this.config.url +'/api/devices/user/binding', {'uid': this.config.uid})
     .map(res=> {
       console.log(res.json())
       var dv = res.json();
      for(let d of dv){
        var split = d.did.split("-")
        d.sdid = split[split.length-1]
      }
      return dv;
     })

  }

  ngOnInit() {
   
  }

  goToLive(did){
    
    var data = {
      'did': did,
      'uid': this.config.uid
    }

    console.log(data)
    this.router.navigate(['/device-live/live', data] )
  }

}



