import {AngularFireDatabaseModule} from 'angularfire2/database';
import {LoginGuard} from './login/login-guard';
import { DashboardComponent } from './dashboard/dashboard.component';
import { SearchDevicesComponent } from './search-devices/search-devices.component';
import {Config} from './config';
import { DeviceListComponent } from './device-list/device-list.component'
import {DeviceLiveModule} from './device-live/device-live.module'


export const firebaseconfig = {
    apiKey: "AIzaSyB_TKkQ-I8WdssJMP8qxeGoxdB01gjWLDs",
    authDomain: "greenhouse-e4197.firebaseapp.com",
    databaseURL: "https://greenhouse-e4197.firebaseio.com",
    projectId: "greenhouse-e4197",
    storageBucket: "greenhouse-e4197.appspot.com",
    messagingSenderId: "243165154492"
  };

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    DashboardComponent,
    SearchDevicesComponent,
    DeviceListComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule,
    AngularFireModule.initializeApp(firebaseconfig),
    AngularFireDatabaseModule,
    DeviceLiveModule
  ],
  providers: [AngularFireAuthProvider,LoginGuard,Config],
  bootstrap: [AppComponent]
})
export class AppModule { }



