import { Component, OnInit } from '@angular/core';
import { DataService } from '../share/data.service'
import {Router,ActivatedRoute} from '@angular/router';
import {SocketService} from '../share/socket.service'
@Component({
  selector: 'app-live-data-component',
  templateUrl: './live-data-component.component.html',
  styleUrls: ['./live-data-component.component.css']
})
export class LiveDataComponentComponent implements OnInit {
  
  constructor(public data:DataService, private router:Router,private route:ActivatedRoute,private io:SocketService) { 
   console.log(this.route.snapshot.params)
    if(this.data.did == undefined){
      this.data.did = this.route.snapshot.params['did'];
    }
    this.io.socket.emit('JOIN_USER', this.route.snapshot.params)
    this.io.socket.emit('REQ_CONFIG', { 'did': this.data.did})
    this.io.socket.emit('START_STREAM', { 'did': this.data.did})
  }

  ngOnInit() {
    
  }

  toGraph():void{
    this.router.navigate(['device-live/live/realtime']);
  }

  toTable():void{
    this.router.navigate(['device-live/live']);
  }




}



