import { Component, OnInit } from '@angular/core';
import {Http,Response} from '@angular/http'
import {Router} from '@angular/router';
import {DataService} from '../share/data.service';
import {GraphService} from './graph.service';
import {ConfigService} from '../share/config.service' 
import {SocketService} from '../share/socket.service'

@Component({
  selector: 'app-data',
  templateUrl: './data.component.html',
  styleUrls: ['./data.component.css']
})

export class DataComponent {
  sensorsData:any;
  yearList:any = []
  monthList:any= []
  month:number=1;
  year:number=2017;
  dateList:any = [];
  
  ngOnInit() {
    var year = 2017;

    for(var i = 1 ; i <= 16; i++){
      this.yearList.push(year++);
    }

    for(var i = 1 ; i <= 12; i++){
      this.monthList.push(i);
    }
  }
  constructor(private http:Http,private route:Router,private config:ConfigService, private io:SocketService,private data:DataService){
    // http.get("http://localhost:3000/api/getdata?date=121017").subscribe((data:Response)=>{
    //   this.sensorsData = data.json();
    // })

    this.io.socket.on('REP_CHECK_DATE',(data:any)=>{
        console.log("REP_CHECK_DATE")
        this.dateList = data;
        console.log(this.dateList)
    })
  }

  checkMonth(val):void{
    this.month = val;
    this.checkDate(this.month,this.year)
  }

  checkYear(val):void {
    this.year = val;
    this.checkDate(this.month,this.year)
  }

  checkDate(month,year):void{
    var m = (month<10)?"0"+month:month;
    var y = year - 2000;
    var str =  m + y;
    this.http.get(this.config.hostname + "/api/checkdate?my="+str)
      .subscribe(data=>{
        this.dateList = data.json();
      })

    this.io.socket.emit('REQ_CHECK_DATE', {'data': str, 'did': this.data.did})

  }

  getDataFromDate(filename):void{
    this.route.navigate(['device-live/datalogger/table'],{ queryParams: { filename: filename } });
  }
  
}



