import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { LiveDataComponentComponent } from '../live-data-component/live-data-component.component'
import { SetPointComponent } from '../set-point/set-point.component'
import {ControlComponent} from '../control/control.component';
import {SettingsComponent} from '../settings/settings.component'
import {DisconnectComponent} from '../disconnect/disconnect.component';
import {ConnectionGuard} from '../disconnect/connection-guard'
import {DataComponent} from '../data/data.component';
import {SensortableComponent} from '../sensortable/sensortable.component'
import {TableComponent} from '../sensortable/table/table.component'
import {GraphComponent} from '../sensortable/graph/graph.component'

import {RealtimegraphComponent} from '../live-data-component/realtimegraph/realtimegraph.component';
import {LoginGuard} from '../../login/login-guard'
import {DeviceLiveComponent} from '../device-live.component'
const routes: Routes= [
	{ path: '', redirectTo: 'device-live', pathMatch: 'full' },
  {path: 'device-live', component: DeviceLiveComponent ,canActivate: [LoginGuard], 
    children: [
      { path: 'live',  component: LiveDataComponentComponent ,canActivate: [LoginGuard] },
      { path: 'control',  component: ControlComponent ,canActivate: [LoginGuard]},
      { path: 'control',  component: ControlComponent ,canActivate: [LoginGuard]},
      { path: 'setting',  component: SettingsComponent ,canActivate: [LoginGuard]},
      { path: 'data',  component: DataComponent ,canActivate: [LoginGuard]},
      { path: 'datalogger',  component: SensortableComponent ,canActivate: [LoginGuard],
        children:[
          { path: 'table',  component: TableComponent ,canActivate: [LoginGuard]},
          { path: 'graph',  component: GraphComponent ,canActivate: [LoginGuard]}
        ]
      },
    ]
}
// ,
// 	{ path: 'live',  component: LiveDataComponentComponent, canActivate: [LoginGuard] ,
//     children: [
//       { path: 'realtime',  component: RealtimegraphComponent, canActivate: [ConnectionGuard] }
//     ]
//    },
	// { path: 'control',  component: ControlComponent, canActivate: [ConnectionGuard]},
  // { path: 'setting',  component: SettingsComponent, canActivate: [ConnectionGuard]},
  // { path: 'data',  component: DataComponent, canActivate: [ConnectionGuard]},
  // { path: 'datalogger',  component: SensortableComponent, canActivate: [ConnectionGuard],
  //   children:[
  //     { path: 'table',  component: TableComponent, canActivate: [ConnectionGuard]},
  //     { path: 'graph',  component: GraphComponent, canActivate: [ConnectionGuard]}
  //   ]
  // },
//   { path: 'disconnected',  component: DisconnectComponent }
]

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
  	RouterModule
  ]
})
export class AppRoutingModule { }



