import { Component, OnInit } from '@angular/core';
import {AngularFireAuthProvider,AngularFireAuth } from 'angularfire2/auth'
import {Router} from '@angular/router'
import {LoginGuard} from '../login/login-guard'

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  uid:string;
  email:string;
  constructor(private afAuth:AngularFireAuth, private router:Router,private loginguard:LoginGuard) { 

  }

  ngOnInit() {
    this.afAuth.authState.subscribe(s=>{
      
      if(!s){
        this.router.navigateByUrl('/login')
        return;
      }

      this.uid = s.uid
      this.email = s.email
    })
  }

  logout(){
    this.afAuth.auth.signOut()
    this.loginguard.isLoggedIn = false;
    this.router.navigateByUrl('/login')
  }

  toSearch(){
    this.router.navigateByUrl('/search')
  }

}



