import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import {LoginComponent} from './login/login.component'
import {LoginGuard} from './login/login-guard'
import {DashboardComponent} from './dashboard/dashboard.component'
import {SearchDevicesComponent} from './search-devices/search-devices.component'
import {DeviceListComponent} from './device-list/device-list.component'


const routes: Routes= [
	{ path: '', redirectTo: '/dashboard', pathMatch: 'full' },
    { path: 'login',  component: LoginComponent},
    { path: 'dashboard',  component:DashboardComponent , canActivate: [LoginGuard]},
    { path: 'search',  component:SearchDevicesComponent , canActivate: [LoginGuard]},
    { path: 'device-list',  component:DeviceListComponent , canActivate: [LoginGuard]}
]

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
  	RouterModule
  ]
})
export class AppRoutingModule { }



